// Animation data
* {

    margin: 0;

    &[data-animation="closeSlideLeft"] {
        animation: closeSlideLeft .5s ease-in forwards;
    }

    &[data-animation="closeSlideRight"] {
        animation: closeSlideRight .5s ease-in forwards;
    }

    &[data-animation="comeSlideLeft"] {
        animation: comeSlideLeft .5s ease-in forwards;
    }

    &[data-animation="comeSlideRight"] {
        animation: comeSlideRight .5s ease-in forwards;
    }

    &[data-animation="closeSlideLeftDesktop"] {
        animation: closeSlideLeftDesktop .5s ease-in forwards;
    }

    &[data-animation="comeSlideLeftDesktop"] {
        animation: comeSlideLeftDesktop .5s ease-in forwards;
    }

    &[data-animation="comeSlideLeftDelay"] {
        animation: comeSlideLeft .5s 4.5s ease-in forwards;
    }

    &[data-animation="closeSlideRightDelay"] {
        animation: closeSlideRight .5s 4.5s ease-in forwards;
    }

    &[data-animation="comeSlideUp"] {
        animation: comeSlideUp .5s ease-in forwards;
    }

    &[data-animation="closeSlideUp"] {
        animation: closeSlideUp .5s ease-in forwards;
    }
}

// Animation boutton croix
@keyframes bar--1 {
    0% {
        top: -13.5px;
    }

    50% {
        top: 0;
        transform: rotateZ(0);
    }

    100% {
        top: 0;
        transform: rotateZ(45deg);
    }
}

@keyframes bar--2 {
    0% {
        top: 13.5px;
    }

    50% {
        top: 0;
        transform: rotateZ(0);
    }

    100% {
        top: 0;
        transform: rotateZ(-45deg);
    }
}

@keyframes bar--3 {
    0% {
        top: 0;
        transform: rotateZ(45deg);
    }

    50% {
        top: 0;
        transform: rotateZ(0);
    }

    100% {
        top: -13.5px;
    }
}

@keyframes bar--4 {
    0% {
        top: 0;
        transform: rotateZ(-45deg);
    }

    50% {
        top: 0;
        transform: rotateZ(0);
    }

    100% {
        top: 13.5px;
    }
}

//Animation globale
@keyframes comeSlideRight {
    from {
        transform: translateX(100%)
    }

    to {
        transform: translateX(0)
    }
}

@keyframes comeSlideLeft {
    from {
        transform: translateX(-100%)
    }

    to {
        transform: translateX(0)
    }
}

@keyframes closeSlideRight {
    from {
        transform: translateX(0%)
    }

    to {
        transform: translateX(100%)
    }
}

@keyframes closeSlideLeft {
    from {
        transform: translateX(0)
    }

    to {
        transform: translateX(-100%)
    }
}

@keyframes comeSlideLeftDesktop {
    from {
        transform: translateX(-35%)
    }

    to {
        transform: translateX(0)
    }
}

@keyframes closeSlideLeftDesktop {
    from {
        transform: translateX(0)
    }

    to {
        transform: translateX(-35%)
    }
}

@keyframes anim {
    from {
        stroke-dashoffset: 2000;
    }

    to {
        stroke-dashoffset: 0;
    }
}

//General settings
body {
    line-height: 1.6;
    font-size: 18px;
    font-family: 'Alegreya', serif;
    font-weight: 400;
    color: #333;
    margin: 0 auto;
    position: relative;

    overflow-x: hidden;

    &[data-chapitre="intro"] {
        .chapitre--intro {
            display: block;
        }

        .btn__text--nav {
            background-color: $c-dark;

            &:after {
                background-color: $c-dark;
            }

            &:before {
                background-color: $c-dark;
            }
        }
    }

    &[data-chapitre="debuts"] {
        .chapitre--debuts {
            display: block;
        }

        .btn__text--nav {
            background-color: $c-dark;

            &:after {
                background-color: $c-dark;
            }

            &:before {
                background-color: $c-dark;
            }
        }
    }

    &[data-chapitre="prohibition"] {
        .chapitre--prohibition {
            display: block;
        }
    }

    &[data-chapitre="souffle"] {
        .chapitre--souffle {
            display: block;
        }
    }

    &[data-chapitre="jours"] {
        .chapitre--jours {
            display: block;
        }
    }

    &[data-load="true"] {
        overflow: hidden;

        .addon--load {
            opacity: 1;

            svg {

                animation: anim 1.8s ease-in;
            }
        }
    }


    &[data-nav="true"] {
        overflow: hidden;

        .addon--nav {
            pointer-events: all;
        }


        .chapitre {
            position: fixed;
            transition: all .5s ease-in;

        }

        .btn__text--nav {
            background-color: transparent;

            transition: all .1s ease-in;

            &:before {
                background-color: $c-white;

                animation: bar--1 .3s forwards;

                transition: all .4s ease-in;
            }

            &:after {
                background-color: $c-white;

                animation: bar--2 .3s forwards;

                transition: all .4s ease-in;
            }
        }
    }

    &[data-info="true"] {
        overflow: hidden;
    }

    &[data-desknav="true"] {
        .addon--nav {
            animation: comeSlideLeft.5s ease-in forwards;
        }
    }

    &[data-page^="page-"] {

        .nav {
            &__container {
                justify-content: center;
            }

            &__title {
                margin-bottom: 50px;
            }
        }

        .chapitre {
            display: block;
        }

        .btn {
            &--nav {
                position: fixed;
            }

            &__text {
                &--nav {
                    background-color: $c-dark;

                    &:before {
                        background-color: $c-dark;

                    }

                    &:after {
                        background-color: $c-dark;

                    }

                }
            }
        }

        &[data-nav="true"] {
            .btn {
                &__text {
                    &--nav {
                        background-color: transparent;

                        &:before {
                            background-color: $c-white;

                        }

                        &:after {
                            background-color: $c-white;

                        }

                    }
                }
            }
        }
    }

    &[data-page="page-propos"] {
        h2 {
            margin-bottom: 10px
        }
    }
}

