//Addon settings
.addon {
    width: 100vw;
    height: calc(100vh - 20px);
    position: fixed;
    top: 0;
    left: 0;

    &--nav {
        padding: 20px 0px 0px 0px;
        background-image: url(../assets/images/landing--menu-min.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        color: $c-white;
        z-index: 20;

        pointer-events: none;
    }

    &--message {
        z-index: 30;
        height: 100vh;

        background: url(../assets/images/landing--sound-min.jpg);
        background-repeat: no-repeat;
        background-size: cover;

        display: flex;
    }

    &--load {
        opacity: 0;
        pointer-events: none;

        transition: opacity .3s ease-out;

        height: 100vh;
        background-color: $c-bg;
        z-index: 80;

        display: flex;

        svg {
            margin: auto;

            height: 200px;
            width: 200px;

            stroke-dasharray: 2000;
            stroke-dashoffset: 2000;

        }
    }

    &--info {
        min-height: 100vh;
        max-height: 100%;
        background-color: $c-bg;
        z-index: 15;
        overflow-y: scroll;
    }

    &--recette {
        min-height: 100vh;
        max-height: 100%;
        background-color: $c-bg;
        z-index: 15;
        overflow-y: scroll;
    }
}

