.message {
    margin: auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__logo {
        width: 40%;
    }

    &__content {
        font-family: 'Montserrat', sans-serif;
        text-align: center;
        color: $c-white;
        margin-top: 30px;

    }
}

//BTN settings
.btn {
    padding: 0;
    border: none;
    background: transparent;

    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;

    cursor: pointer;

    &:focus {
        outline: 0;
    }

    &--nav {
        position: absolute;
        height: 30px;
        width: 30px;
        top: 15px;
        right: 15px;

        z-index: 25;
    }

    &__text {
        &--nav {
            height: 3px;
            width: 30px;
            background-color: $c-white;
            position: absolute;
            top: 13.5px;
            left: 0;

            transition: all .3s ease-out;

            &:before {
                content: '';
                position: absolute;
                height: 3px;
                width: 30px;
                top: 13.5px;
                left: 0;
                background-color: $c-white;
                animation: bar--3 .3s forwards;
                transition: all .3s ease-out;

            }

            &:after {
                content: '';
                position: absolute;
                height: 3px;
                width: 30px;
                top: -13.5px;
                left: 0;
                background-color: $c-white;
                animation: bar--4 .3s forwards;
                transition: all .3s ease-out;

            }

        }
    }

    &--close {
        background-image: url("../assets/images/svg/svg--cross.svg");
        background-repeat: no-repeat;
        background-size: cover;

        transform: rotate(-90deg);

        height: 40px;
        width: 40px;

    }

    &__arrow {
        background-image: url("../assets/images/svg/svg--arrow.svg");
        background-repeat: no-repeat;
        background-size: cover;

        height: 50px;
        width: 50px;

        &--white {
            background-image: url("../assets/images/svg/svg--arrow--white.svg");
        }
    }

    &--info {
        position: sticky;
        position: -webkit-sticky;
        top: 25px;
        left: 0;
        z-index: 30;
        margin-left: 85%;

    }

    &--music {
        position: fixed;
        top: 10px;
        left: 10px;
        padding: 5px 10px;
        border: 3px solid $c-dark;
        background-color: $c-bg;
        transition: .3s all ease-out;

        z-index: 10;
    }
}

//Navigation settings
.nav {

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: inherit;
    }

    &__title {
        padding-left: 20px;
        line-height: 1.2;
    }

    &__el {
        overflow: hidden;
        position: relative;
        padding-left: 20px;

        p {
            font-family: 'Montserrat', sans-serif;
            color: darken($c-white, 30%);
        }
    }

    &__dots {
        display: none;
    }

    &__heaj {
        display: none;
    }

    &__home {
        display: none;
    }

    &__progress {
        display: none;
    }

    &__content {
        background-image: linear-gradient($c-red 33%, rgba(226, 35, 35, 0) 0%);
        background-position: left;
        background-size: 2px 5px;
        background-repeat: repeat-y;

        padding-bottom: 20px;
        padding-left: 30px;

        display: inline-block;

        &:before {
            content: '';
            width: 10px;
            height: 10px;
            background: $c-red;
            position: absolute;
            left: 16px;
            top: 1ch;
            border-radius: 100%;
        }

        &--active {
            &:after {
                content: '';
                width: 14px;
                height: 14px;
                background: transparent;
                border: $c-red solid 1px;
                position: absolute;
                left: 13px;
                top: .7ch;
                border-radius: 100%;
            }
        }

        p {
            font-size: .8em;
        }
    }

    &__links {
        text-align: center;
    }

    &__link {
        color: $c-red;
        border-right: 1px solid $c-red;
        padding: 0px 15px;

        &:last-child {
            border: none;
        }
    }
}

//Box and chapitre settings
.chapitre {
    width: 100%;
    display: none;

    &--intro {
        position: fixed;
    }

    &--aside {
        background-color: $c-bg;

        h2 {
            font-size: 2.8em;
            margin-bottom: 32px;
        }

        h3 {
            font-size: 2.2em;
            line-height: 1;
            margin-bottom: 32px;
        }

        h4 {
            font-size: 1.4em;
        }

        a {
            position: relative;
            transition: all .3s ease-in;
            padding-bottom: 2px;

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: $c-red;
                transform: scaleX(0);
                transform-origin: center center;
                transition: all .3s ease-in;
            }

            &:hover {
                color: $c-red;
                transition: all .3s ease-in;

                &:before {
                    transform: scaleX(1);
                    transition: all .3s ease-in;
                }
            }
        }
    }
}

