//Tablet
@media only screen and (min-width: 600px) {
    body {
        font-size: 22px;
    }

    .sansserif {
        font-size: 20px;
    }

    .nav {
        &__title {
            text-align: center;
        }

        &__list {
            all: unset;
            list-style-type: none;
            width: 100%;
            margin: 0 auto;
        }

        &__content {
            all: unset;

