            float: left;
            border-right: 3px dotted $c-red;
            margin-right: -3px;
            clear: both;
            text-align: right;
            margin-bottom: 3px;

            &--right {
                float: right;
                text-align: left;
                padding-left: 20px;
                padding-right: 0;
