        }
    }

    .nav {
        &__title {
            display: none;
        }

        &__links {
            display: none;
        }

        &__container {
            display: block;

            height: 100vh;
            width: 60px;
        }

        &__list {
            all: initial;

            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateY(-50%);

            &:before {
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAECAYAAABP2FU6AAAAEUlEQVQYV2O4xmf8nwFMwAEAPggELf4HomYAAAAASUVORK5CYII=) repeat;
                bottom: 50px;
                content: "";
                display: block;
                left: -1px;
                position: absolute;
                top: 10px;
                width: 2px;
                height: 95%;

            }
        }

        &__content {
            all: initial;

            opacity: 1;
            pointer-events: none;

            color: transparent;
            
            margin-bottom: 20px;

            background: rgba(31, 35, 43, 1);

            margin-left: 40%;
            display: inline-block;

            padding: 20px 20px;

            position: relative;

            transition: transform .3s ease-in, color .3s .1s ease-out;

            transform: translateX(-70px) scaleX(0);
            transform-origin: center left;

            &:before {
                content: '';
                position: absolute;
                left: -49px;
                top: -0.5px;
                width: 50px;
                height: 101.5%;
                clip-path: polygon(0% 50%, 100% 100%, 100% 0%);
                background-color: #1f232b;
            }

            p {
                font-family: 'Montserrat', sans-serif;
                font-size: .8em;
            }

            h2 {
                white-space: nowrap;
                color: $c-red;
            }

            &--active {

                color: #fff;
                transform: translateX(0px) scaleX(1);

                transition: transform .3s ease-out, color .3s .1s ease-in;
            }
        }

        &__logo {
            width: 100%;
            height: 100%;
        }

        &__heaj {
            display: block;

            height: 30px;
            width: 105px;

            z-index: 5;

            position: absolute;
            left: 45px;
            bottom: 20px;

            transform: rotate(-90deg);
            transform-origin: bottom left;
        }

        &__home {
            display: block;

            height: 30px;
            width: 76.5px;

            z-index: 5;

            position: absolute;
            left: 45px;
            top: 75px;

            transform: rotate(-90deg);
            transform-origin: bottom left;
        }

        &__el {
            all: initial;

            position: relative;

            &--right {
                all: initial;
                position: relative;
            }
        }

        &__dots {
            display: block;
            position: absolute;
            top: 0;
            left: 0;

            cursor: pointer;

            &:before {
                background: #d60e33;
                border-radius: 50%;
                content: "";
                display: block;
                height: 12px;
                left: -6px;
                position: absolute;
                top: -3px;
                width: 12px;
            }

            &--active {
                &:after {
                    width: 16px;
                    height: 16px;
                    border: 2px solid #d60e33;
                    border-radius: 50%;
                    content: "";
                    display: block;
                    left: -10px;
                    position: absolute;
                    top: -7px;
                }
            }
        }

        &__progress {
            display: block;
            width: 5px;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
        }

        &__bar {
            height: 0%;
            background-color: $c-red;
        }
    }

    .addon {
        &--info {
            width: 35%;
            right: 0;
            top: 0;
            left: auto;
        }

        &--recette {
            width: 35%;
            right: 0;
            top: 0;
            left: auto;
        }

        &--nav {
            all: initial;

            display: block;
            position: fixed;
            top: 0;
            left: 0;
            background-color: rgba(31, 35, 43, 0.94);
            z-index: 2;
            animation: closeSlideLeft .5s ease-out forwards;
        }

        &--message {
            background: url(../assets/images/desktop/landing--sound-min.jpg);
            background-size: cover;
        }
    }

    .landing {
        &__head {
            width: 100%;
        }

        &__title {
            font-size: 4.5em;
        }

        &__sub {
            font-size: 1.5em;
        }

        &__scroll {
            p {
                &:nth-child(1) {
                    font-size: 1.5em;
                }

                &:nth-child(2) {
                    font-size: 2.5em;
                }
            }
        }

        &__chapitre {
            h2 {
                font-size: 4.5em;
            }

            p {
                font-size: 1.5em;
            }
        }
    }

    .btn {
        transition: all .3s ease-in;

        &--close {
            &:hover {
                transform: rotate(90deg);
                transition: all .3s ease-in;
            }
        }

        &--nav {
            display: none;
        }

        &--music {
            left: auto;
            right: 10px;

            &:hover {
                color: $c-bg;
                background-color: $c-dark;
                transition: .3s all ease-in;
            }
        }
    }

    .flair {
        margin-bottom: 0;
        width: 45%;
    }

    .link {

        position: relative;
        transition: .3s all ease-in;

        &:before {
            content: '';
            position: absolute;
            bottom: -2px;
            left: 0;
            width: 100%;
            height: 1px;
            background: $c-red;
            transition: .3s all ease-in;
            transform-origin: center center;
            transform: scaleX(.4);
        }

        &:hover {
            &:before {
                height: 2px;
                transform: scaleX(1);
                transition: .3s all ease-in;
            }
        }
    }

    .more {
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;

