        height: 40vh;
    }

    .recette {

        &__background {
            height: 40vh;
        }
    }

    .rencontre {
        &__content {
            width: 100%;
            margin: auto;
        }

        &__title {
            font-size: 3em;
        }
    }

    .message {
        &__logo {
            width: 100px;
            height: auto;
        }

        &__content {
            width: 30%;
        }
    }

    .citation--jours {
        color: $c-white;
    }

    .video {
        display: block;
    }
}

